<!-- by 添少 -->
<template>
  <section class="ui-tab">
    <ul>
      <template v-for="(row,$index) in data">
        <li :class="{active:select===$index}" @click="handSelect($index)">{{row}}</li>
      </template>
    </ul>
  </section>
</template>

<script>
  export default {
    name: 'tab',
    props: {
      select: {
        type: Number,
        default: 0
      },
      data: {
        type: Array,
        default: () => []
      }
    },
    data () {
      return {}
    },
    methods: {
      handSelect (index) {
//        this.select = index
        this.$emit('update:select', index)
        this.$emit('change', index)
      }
    }
  }
</script>

<style lang="scss" scoped>
  .ui-tab {
    background-color: #fff;
    text-align: center;
    color: #555;
    ul {
      display: flex;
      li {
        padding-top: 10px;
        padding-bottom: 10px;
        flex: 1;
        border-bottom: solid #e5e5e5 1px;
        &.active {
          border-bottom-color: #1797c6;
        }
      }
    }
  }
</style>



