<template>
  <div class='forumListCon'>
    <hr />
    <template v-if="forumLists.length>0">
      <div class='forumList' v-for='item in forumLists' :key="item.discussId" @click='gotopage(item.discussId)'>
        <div class='userInfo'>
          <div class="headImg" :style="{backgroundImage:'url('+item.headimg+')'}"></div>
          <div class='InfoCon'>
            <div class='name'>{{item.nickname}}<span :class="['usertype',item.userType==3&&'blue',item.userType==4&&'red',item.userType==5&&'red']" v-text='showUserType(item.userType)'></span></div>
            <div class='describe'>
              <span class='time' v-text='item.publishDate'></span>板块：
              <span class='type' v-text='item.tagName'></span>
            </div>
          </div>
        </div>
        <div class='text' v-text='item.cont'></div>
        <div class='imgCon' v-if='item.preimg'>
          <div v-for='imgSrc in item.preimg.split("#")'  @click='showImg(item.preimg.split("#"))' v-if='imgSrc' :style="{backgroundImage:'url('+imgSrc+')'}" :key="imgSrc.id"></div>
          <div v-if='item.preimg.split("#").length==3'></div>
        </div>
        <div class='other'>
          <span class='commentBtn'></span>
          <span :class="['iconfont AddPraiseImg',item.like==2&&'active']" @click='addLike(item)'></span>{{item.likeNum}}
        </div>
        <div class='commentCon' v-if='item.commentList.length>0'>
          <div v-for='info in item.commentList' :key="info.commentId">
            <p v-if='info.userId==info.atUserId||info.atUserId==0'>
              <span class='name' v-text='info.nickname'></span>:
              <span v-text='info.comment'></span>
            </p>
            <p v-else>
              <span class='name' v-text='info.nickname'></span>回复<span class='name' v-text='info.atNickname'></span>:
              <span v-text='info.comment'></span>
            </p>            
          </div>
        </div>
          <template v-else>
            <hr class='commentline'> 
          </template>
      </div>
    <hr/>
    </template>

  </div>

</template>
<script>
export default {
    name:'',
    props:{
      forumLists:{},
    },
    data () {
        return {
        }
    },
     computed:{
    },
    methods:{
      showUserType(data){
        switch (data) {
          case 1:
            return '游客';
            break;
          case 2:
            return '达人';
            break;
          case 3:
            return '教练';
            break;
          case 4:
            return '俱乐部';
            break;
          case 5:
            return '俱乐部';
            break;
          default:
            break;
        }
      },
      showImg(src){
        event.stopPropagation();
        this.$bus.$emit('showImg', src);
      },
      gotopage(id){
        this.$router.push(`/forumPage/forumDetailsPage/${id}`);
      },
      addLike(_obj){
        event.stopPropagation();
				if(_obj.like==1){
					this.$http.post(`${this.$port.ADDLIKE_PROT}?contId=${_obj.discussId}`,{
					}).then((response) => {
						const {data} = response;
						if(response.code == 200){
							_obj.likeNum++;
							_obj.like=2;
						}
					})
					.catch((error) => {
						console.log(error.response.data);
					});
				}
      }
    },
}
</script>

<style lang="scss" scoped>
    *{
      border: none;
      margin: 0;
      padding: 0;
    }
    .forumListCon{
      width:6.9rem;
      margin:0 0.3rem;
      hr{height:1px;}
      .forumList{
        position: relative;
        margin:0.36rem 0;
        overflow:hidden;
        .clickBtn{
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
        }
        .userInfo{
          display: flex;
          .headImg{
              width: 0.72rem;
              height: 0.72rem;
              background-size: 0.72rem 0.72rem;
              background-repeat: no-repeat;
              border-radius: 0.72rem; 
              margin-right: 0.24rem;
          }
          .InfoCon{
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-content:space-between;
            .name{
                font-size: 0.28rem;
                color: #333333;
                .usertype{
                  color:#1797c6;
                  margin: 0 0.1rem;
                  padding: 0 0.15rem;
                  border-radius: 0.36rem;
                  color: white;
                  background: #edaa36;
                  &.red{
                    background: #d33c44;
                  }
                  &.blue{
                    background: #1797c6;
                  }
                }
            }
            .describe{
                font-size: 0.22rem;
                color: #888888;         
                .time{
                  margin-right:0.08rem;
                }    
                .type{
                  color:#1797c6;
                }       
                .removeBtn{
                  position: relative;
                  float:right;
                  color:#1797c6;
                }       
            }         
          }
        }
        .text{
          font-size:0.32rem;
          line-height:0.55rem;
          margin:0.24rem 0;
        }
        .imgCon{
          width:100%;
          height:1.8rem;
          display:flex;
          justify-content:space-between;
          margin-bottom:0.24rem;
          div{
            width:2.2rem;
            height:1.8rem;
            background-size: cover;
            background-position:center;
          }
        }
        .other{
          position: relative;
          line-height:0.28rem;
          font-size:0.38rem;
          color:#1797c6;
          float:right;
          margin-right:0.3rem;
          .AddPraiseImg{
            font-size:0.38rem;
            margin:0 0.13rem 0 0.34rem;
            &:before{
              margin-right: 0.1rem;
              content:'\e870';
            }
            &.active:before{
              margin-right: 0.1rem;
              content:'\e86f';
            }
          }
          .commentBtn{
            display: inline-block;
            width:0.28rem;
            height:0.27rem;
            background:url('../../../assets/images/commentImg.png') no-repeat;
            background-size: contain;
          }
        }
        .commentCon,.commentline{
          position: relative;
          width:100%;
          margin-top:0.7rem;
          clear:right;
          background:#f3f3f5;
          font-size:0.3rem;
          color:#333333;
          line-height:0.48rem;
          text-indent: 0.08rem;
          .name{
            color:#1797c6;
          }
        }
        .commentline{
          margin-top: 0.7rem;
          border-bottom-style: double;
          border-bottom-width: 1px;
          background: none;
          border-bottom-color: #e5e5e5;
        }
      }
    }
</style>




