<!-- by 添少 -->
<template>
  <section class="container">
    <v-Header class="head">
      <span slot='text'>{{title}}详情</span>
    </v-Header>
    <router-view></router-view>
    <section class="detail">
      <tab :select.sync="tab.select" :data="tabList" @change="handChangeTab"></tab>
      <section class="tc1" :style="{ 'min-height' :minHeight}">
        <template v-if="list.length">
          <template v-if="tab.select===0||tab.select===1||tab.select===2">
            <rendezvous-list :Lists="list"></rendezvous-list>
            <template v-if="isNone">
              <p style="text-align: center;padding: 10px;">没有更多了</p>
            </template>
          </template>
          <template v-else-if="tab.select===3">
            <forum-list :forumLists="list"></forum-list>
          </template>
        </template>
        <template v-else>
          <p style="text-align: center;padding: 10px;">
            暂无内容
            <template v-if="userInfo.userId===userId">
              <a href="javascript:;"
                 @click="handWrite(tab.list[tab.select].value)">立即发布{{tab.list[tab.select].label}}</a>
            </template>
          </p>
        </template>
      </section>
    </section>
  </section>
</template>

<script>
  import {mapState} from 'vuex'
  import Header from '../../components/header';
  import FixedLayout from '@/components/fixed-layout'
  import Topbar from '@/components/topbar'
  import Tab from '@/components/tab'
  import RendezvousList from '@/views/rendezvous/rendezvousList'
  import ForumList from '@/views/detail/components/forumList'

  export default {
    name: 'coach',
    components: {
      FixedLayout,
      Topbar,
      Tab,
      RendezvousList,
      ForumList,
      'v-Header': Header,
    },
    data() {
      return {
        tab: {
          select: 0,
          list: [
            {value: 5, label: '课程'},
            {value: 3, label: '行程'},
            {value: 4, label: '装备'},
            {value: 2, label: '帖子'},
          ]
        },
        list: [],
        lastProductId: 0,
        isNone: false,
        loading: false,
      }
    },
    watch: {
      id() {
        this.fn_on_bottom()
      }
    },
    computed: {
      ...mapState({
        userInfo: state => state.userInfo,
        title: state => state.detail.title,
        id: state => state.detail.id,
        userId: state => state.detail.userId,
      }),
      minHeight() {
        return (this.list.length == 0 && this.isNone != true && this.loading == true ? '13.34rem' : '0');
      },
      tabList() {
        let list = []
        this.tab.list.forEach(e => list.push(e.label))
        return list
      }
    },
    created() {
      window.addEventListener('scroll', this.fn_on_bottom)
      this.fn_on_bottom()
    },
    methods: {
      init() {
      },
      handChangeTab(index) {
        this.list = []
        this.lastProductId = 0
        this.isNone = false
        this.loading = false
        this.$nextTick(() => {
          this.fn_on_bottom()
        })
        // this.getList()
      },
      getList() {
        console.log('try get')
        if (this.id === 0) return
        console.log('try succ')
        if (this.tab.select === 3) {
          // 帖子
          this.loading = true;
          this.$http.get(this.$port.GET_DISCUSS_LIST, {
            params: {
              userId: this.id,
              lastDiscussId: this.lastProductId
            }
          }).then(res => {
            if (res.code === 200) {
              if (res.data.length && res.data[res.data.length - 1].discussId !== this.lastProductId) {
                this.list = this.list.concat(res.data)
                this.lastProductId = res.data[res.data.length - 1].discussId
                this.isNone = false
                this.loading = false;
              } else {
                this.isNone = true
              }
            }
          })
        } else {
          this.loading = true;
          let params = {
            userId: this.id,
            lastProductId: this.lastProductId
          }
          params.contType = this.tab.list[this.tab.select].value
          this.$http.get(this.$port.GET_PRODUCT_LIST, {
            params
          }).then(res => {
            if (res.code === 200) {
              if (res.data.length && res.data[res.data.length - 1].productId !== this.lastProductId) {
                this.list = this.list.concat(res.data)
                this.lastProductId = res.data[res.data.length - 1].productId
                this.isNone = false
                this.loading = false;
              } else {
                this.isNone = true
              }
            }
          })
        }
      },
      loadMore() {
        this.getList(this.tab.select === 3 && 1)
      },
      handWrite(type) {
        this.$router.push({
          path: '/write/cover',
          query: {
            contType: contType,
          },
        })
      },
      fn_on_bottom() {
        let body = document.documentElement || document.body
        // console.log(body.scrollHeight, body.scrollTop + window.innerHeight)
        if (body.scrollHeight === body.scrollTop + window.innerHeight) {
          console.log('on bottom')
          this.getList()
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  .container {
    padding-top: 41px;
    .head {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
    }
  }

  .layout {
    background-color: #fff;
    margin-bottom: 10px;
  }

  .tc1 {
    background-color: #fff;
    overflow: hidden;
  }

  .m-scroll {
    height: 100%;
    overflow: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
  }
</style>



